/* General layout for tab container */
.tab1-container {
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: #f8f8f8;
}

/* Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Ensure it appears above other content */
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
  max-width: 90%;
  z-index: 1001; /* Ensure it appears above the overlay */
}

.modal-content h2 {
  margin-bottom: 15px;
  font-size: 24px;
}

.modal-content label {
  display: block;
  margin-bottom: 10px;
}

.modal-content input {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
  border-radius: 4px;
  border: 1px solid #ddd;
}

.modal-content button {
  background-color: #4CAF50;
  color: white;
  padding: 10px;
  margin-top: 15px;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.modal-content button:hover {
  background-color: #45a049;
}

.modal-content button:last-child {
  background-color: #f44336;
}

.modal-content button:last-child:hover {
  background-color: #e53935;
}

/* Item Box Layout */
.item-box {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  background-color: white;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.item-image {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 8px;
}

.item-text-container {
  margin-left: 15px;
}

.item-title {
  font-size: 18px;
  font-weight: bold;
}

.item-quantity,
.item-price {
  font-size: 14px;
  margin-top: 5px;
}

/* Plus and Minus Icons */
.plus-icon, .minus-icon {
  font-size: 20px;
  cursor: pointer;
  margin-left: 5px;
}

.plus-icon {
  color: #4CAF50;
}

.plus-icon:hover {
  color: #388e3c;
}

.minus-icon {
  color: #f44336;
}

.minus-icon:hover {
  color: #d32f2f;
}

/* Add Product Button (Sticky) */
.add-product-button {
  flex-direction: column;
  align-items: center;
  margin-bottom: 110px;
  background-color: #4CAF50; /* Green background for the button */
  color: white; /* White text color */
  padding: 12px 20px; /* Button padding */
  border: none; /* Remove border */
  border-radius: 5px; /* Rounded corners */
  cursor: pointer; /* Cursor pointer on hover */
  font-size: 16px; /* Adjust font size */
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 10; /* Ensure the button is on top of other elements */
}

.add-product-button:hover {
  background-color: #45a049; /* Darker shade of green when hovered */
}

/* Review Order Button */
.review-order-button {
  background-color: #f0ad4e;
  color: white;
  padding: 10px 15px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  width: 100%;
  text-align: center;
}

.review-order-button:hover {
  background-color: #ec971f;
}

/* Responsive Layout */
@media (max-width: 768px) {
  .tab1-container {
    padding: 10px;
  }

  .item-box {
    flex-direction: column;
    align-items: flex-start;
  }

  .item-image {
    width: 60px;
    height: 60px;
  }

  .item-text-container {
    margin-left: 0;
  }

  .add-product-button,
  .review-order-button {
    width: 100%;
  }

  /* Adjust icon sizes in mobile */
  .plus-icon, .minus-icon {
    font-size: 18px;
  }
  .huge-plus-icon {
    font-size: 80px;
    color: #000000;
    cursor: pointer;
    transition: transform 0.2s;
  }
  
  .huge-plus-icon:hover {
    transform: scale(1.2);
  }
  
  .plus-button-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
/* Remove Gmail Button (Upper Right) */
.remove-email-button {
  position: absolute;
  top: 20px; /* Distance from the top of the container */
  right: 20px; /* Distance from the right of the container */
  background-color: black; /* Black background */
  color: white; /* White text */
  padding: 10px 15px; /* Padding for the button */
  border: none; /* No border */
  border-radius: 5px; /* Rounded corners */
  font-size: 14px; /* Adjust font size */
  cursor: pointer; /* Pointer on hover */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Subtle shadow for depth */
  z-index: 10; /* Ensure it appears above other content */
  transition: background-color 0.3s ease, transform 0.2s ease; /* Smooth transition effects */
}

.remove-email-button:hover {
  background-color: white; /* White background on hover */
  color: black; /* Black text on hover */
  transform: scale(1.05); /* Slight zoom effect */
}

/* Adjust for responsive layout */
@media (max-width: 768px) {
  .remove-email-button {
    top: 150px; /* Reduce top margin for smaller screens */
    right: 10px; /* Reduce right margin for smaller screens */
    font-size: 12px; /* Adjust font size */
    padding: 8px 12px; /* Adjust padding */
  }
}

}
